
import { defineComponent } from "vue";
import IPedido from "@/interface/IPedido";
import IProduto from "@/interface/IProduto";
import Message from "./Message.vue";

export default defineComponent({
  name: "Dashboard",
  components: { Message },
  data() {
    return {
      pedidos: [] as IPedido[],
      pedido: {} as IPedido,
      status: [] as IProduto[],
      msg: "" as String,
      tipoMsg:"message-container" as String
    };
  },

  methods: {
    async getPedidos() {
      const req = await fetch("https://my-json-server.typicode.com/Paulogsiqueira/HamburgueriaJson/burguers");
      const data = await req.json();

      this.pedidos = data;

      console.log(this.pedidos);

      this.getStatus();
    },
    async deletePedido(num: number) {
      const req = await fetch(`https://my-json-server.typicode.com/Paulogsiqueira/HamburgueriaJson/burguers/${num}`, {
        method: "DELETE",
      });
      const res = await req.json();

      this.msg = `Pedido removido com sucesso!`;

      setTimeout(() => {
        this.msg = "";
      }, 3000);

      this.getPedidos();
    },
    async updatePedido(evento: Event, num: number) {
      const option: any = evento.target;
      const valor: String = option.value;

      const dataJson = JSON.stringify({
        status: valor,
      });
      console.log(valor);

      const req = await fetch(`https://my-json-server.typicode.com/Paulogsiqueira/HamburgueriaJson/burguers/${num}`, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: dataJson,
      })
        
      const res = await req.json();

      this.msg = `Pedido Nº ${res.id} foi atualizado para ${res.status}!`;

      setTimeout(() => {
        this.msg = "";
      }, 3000);

      
    },
    async getStatus() {
      const req = await fetch("https://my-json-server.typicode.com/Paulogsiqueira/HamburgueriaJson/status");
      const data = await req.json();

      this.status = data;
    },
  },
  mounted() {
    this.getPedidos();
  },
});
